import React from 'react'

const SearchResult = ({ findImg, result, styleName }) => {
  const imgFinder = () => {
    findImg(result.data[0].nasa_id, result.data[0].title, result.data[0].description)

  }

  return (
    <div onClick={imgFinder} className={`${styleName} link`}>
      {result.data[0].title}
    </div>)
}


export default SearchResult