import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { imgError } from '../reducers/searchReducer'

const SearchImg = () => {
  const dispatch = useDispatch()

  const handleError = () => {
    dispatch(imgError())
  }

  const search = useSelector(state => state.search)
  const img = search.img
  const alt = search.alt
  const desc = search.desc
  if (search.errored === true) {
    return (
      <div className='img-fail-box' id='apod-img'>
        <i>Image failed to load. Search result raw data:</i>
        <br />
        <div className='img-fail'>
          {img}
        </div>
      </div>
    )
  }

  else {
    return (
      <div id='apod-img'>
        <img onError={handleError} src={img} alt={alt} />
        <br />
        <div className='apod-desc'>
          {desc}
        </div>
      </div>
    )
  }
}


export default SearchImg