import React from 'react'

const Comment = ({ comm, user, deleteComment }) => {

  const handleRemove = async () => {

    deleteComment(comm.id)
  }

  if (comm !== undefined && comm.user !== undefined) {

    return (
      <div className='comment'>
        {user === comm.user.username &&
          <button className='remove-comment-button' onClick={handleRemove}>Remove comment</button>
        }
        &quot;{comm.content}&quot;<br />
        <blockquote>
          by <b>{comm.user.name}</b>
        </blockquote>
      </div>)
  }
  return (<div >
    &quot;{comm}&quot;<br />

  </div>
  )
}

export default Comment