import axios from 'axios'
const baseUrl = 'https://api.nasa.gov/insight_weather/?api_key=ZLkfBiLh7H19ZeM6kNkEWnHa1MnqA6GmkbKK3pug&feedtype=json&ver=1.0'

const get = async () => {
  const res = await axios.get(baseUrl)
  return res.data
}

const getToday = async () => {
  const res = await axios.get(baseUrl)
  const sols = res.data.sol_keys
  const lastIndex = sols.length - 1
  const todayOnMars = sols[lastIndex]
  return todayOnMars
}


export default {
  get,
  getToday
}